.letter {
    opacity: 0;
}

.letter-in {
    display: inline-flex;
    opacity: 1 !important;
    animation: letter .5s linear;
}

.project {
    opacity: 0;
}

.img-in {
    opacity: 1 !important;
    animation: img .6s linear;
}

.githubIcon {
    position: absolute;
    right: 0;
    top: 0;
    width: 40px;
    height: 40px;
    cursor: pointer;
    margin: 10px 10px 0 0;
}

.githubIcon path {
    fill: #f1f1f1;
}

@keyframes img {
    0% {
        opacity: 0;
    }

    100% {
        opacity: 1;
    }
}


@keyframes letter {
    0% {
        opacity: 0;
        transform: scale(0);
    }

    60% {
        transform: scale(1.4);
    }

    100% {
        opacity: 1;
        transform: scale(1);
    }
}

.xdimg {
    margin: 20px 0;
}


.flex1 {
    padding: 0 75px 0 125px;
}

.flex2 {
    padding: 0 125px 0 75px;
}


.active path {
    fill: #2ce8cd !important;
}

.active {
    cursor: default !important;
}

.active:hover path {
    fill: #2ce8cd !important;
}

.skill {
    color: #2ce8cd;
    font-size: 24px;
    flex: 1 0 33%;
    opacity: 0;
}